'use strict';

export default {
    namespaced:true,
    state:{
        sticky:true,
    },
    mutations:{
        sticky(state,data){
            state.sticky=data;
        }
    },
    actions:{
        setSticky({commit,state}){
            commit("sticky",true);
            console.log("STICKY",state.sticky);
        },
        unsetSticky({commit,state}){
            commit("sticky",false);
            console.log("STICKY",state.sticky);
        }
    }
};



