<template>
<b-modal id="authenticate" title="Autenticación" @show="onShow" @ok="sendAuth" ok-title="Enviar" cancel-title="Cancelar" :ok-disabled="!validOption">
    <template v-if="show">
        <api-request v-if="!option" :resource="getOptions" v-model="form">
            <ITDForm key="1" v-if="form" :config="optionsForm" :form="form" v-model="myOption"/>
        </api-request>
        <api-request :trigger.sync="validOption" :resource="getForm" v-model="formAuth">
            <ITDForm key="2" v-if="formAuth && option" :config="optionsForm":form="formAuth" v-model="myAuth"/>
        </api-request>
    </template>
</b-modal>
</template>

<script>
import { createNamespacedHelpers } from 'vuex'
const {mapState, mapActions} = createNamespacedHelpers("auth");
export default {
    name:"auth",
    //props:[""],
    data(){
        return {
            form:null,
            formAuth:[],
            show:false,
            myOption:{
                option:""
            },
            myAuth:{},
            options:[],
            optionsForm:{}
        };
    },
    computed:{
        ...mapState(["auth","userData"]),
        option:{
            get(){
                let opt =  this.$attrs.option||this.myOption.option;
                console.log("computed option",opt);
                return opt;
            },
            set(v){
                this.myOption.option=v;
            }
        },
        validOption(){
            return this.option.length>0;
        },
        model(){
            return this.$attrs.model||"User";
        }
    },
    methods:{
        ...mapActions(["checkLogin"]),
        onShow(evt){
            if(this.auth){
                evt.preventDefault();
                console.log("signout user");
                this.$http.get("/APP/AAA/logout.json").then(()=>location.href="/")
            }else{
                console.log("ejecutando modal auth");
                this.show=true;
            }
        },
        getOptions(){
            //verificamos si hay opción, si no la hay retornamos un select
            return this.$http.post("/APP/AAA/authForm.json",{
                model:this.model 
            }).then(res=>{
                let form = [{
                    type:"select",
                    name:"option",
                    label:"Seleccione la forma de autenticar",
                    value:null,
                    options:res.body
                }];
                console.log("getOptions");
                console.log(form);
                return form;
            });
        },
        getForm(){
            //verificamos si hay opción, si no la hay retornamos un select
            console.log("getForm");
            return this.$http.post("/APP/AAA/authForm.json",{
                model:this.model , option: this.option
            }).then(res=>{
                return res.body;
            })
        },
        sendAuth(){
            this.checkLogin(this.myAuth);
        }
    },
    watch:{
        userData:{
            handler(val){
                if(val.functions){
                    function getComponents(el){
                        //let el = JSON.parse(JSON.stringify(route));
                        let comp = el.component;
                        if(typeof comp === "string"){
                            el.component=()=>import("@/components/"+comp).catch(()=>import(""+comp));
                        }
                        if(el.children) el.children=el.children.map(getComponents);
                        return el;
                    }
                    console.log("generando rutas");
                    this.$router.addRoutes(val.functions.map(func=>{
                        let route = {
                            path:func.path
                        };
                        if(func.route){
                            console.log("la función tiene ruta");
                            console.log(func.route);
                            route = Object.assign(route,func.route);
                            if(func.route.path) route.path=func.path+"/"+route.path;
                        }else{
                            console.log("la función no tiene ruta");
                            if(!func.component){
                                console.log("la función no tiene componente");
                                //consultamos por el valor
                                route.component=()=>{
                                    return this.$http.get(route.path+".vue").then(({body:comp})=>{
                                        return import("@/components/"+comp).catch(()=>import(""+comp));
                                    });
                                };
                            }else{
                                route.component=func.component;
                            }
                        }
                        route.meta=Object.assign(route.meta||{},{description:func.description});
                    
                        let r = getComponents(route);
                        console.log(r);
                        return r;
                    }));
                    this.$router.addRoutes([
                        {
                            path:"*",
                            component: ()=>import("@/components/Error404")
                        }
                    ]);
                    if(val.profile&&val.profile.link) this.$router.push(val.profile.link);
                
                }
            }
        }
    },
    mounted(){
        console.log("auth montado");
        console.log(this.form);
        //verificamos 
        this.checkLogin().then(()=>{
            console.log("checkLogin");
            console.log(this.auth);
            if(!this.auth){
            }
        });
    }
}
</script>



