<template>
    <b-form @submit="$listeners.submit" @reset="$listeners.reset" :inline="'inline' in $attrs">
        <b-form-group v-for="(input,id) in form" :key="id"
            :label="input.label"
            :description="input.description"
            :horizontal="'horizontal' in $attrs"
            :state="input.state"
            :invalid-feedback="input.error"
            valid-feedback=""
            >
            <component v-if="input.component" :is="input.component" v-model="myValue[input.name]"/>
            <ITDForm 
                v-else-if="input.inputs" 
                :form="input.inputs" 
                v-model="myValue[input.name]"
                :config="{submitText:null,resetText:null}"
            />
            <b-input-group 
                v-else
                :left="input.left" 
                :right="input.right"
            >
                <component class="mx-auto" :is="inputType(input)"
                :placeholder="input.placeholder"
                v-model="myValue[input.name]"
                :state="input.state"
                :accept="input.accept||'*'"
                :type="input.type"
                :multiple="input.multiple"
                :options="input.options"
                :buttons="input.buttons"
                :hidden="input.hidden"
                button-variant="outline-primary"
                />
            </b-input-group>
            <hr v-if="input.validate && input.validate.double_check">
            <b-input-group v-if="input.validate && input.validate.double_check" 
                :left="input.left" 
                :right="input.right"
            >
                <component class="mx-auto" :is="inputType(input)"
                :placeholder="'Repita '+input.placeholder"
                v-model="double_check[input.name]"
                :state="input.state"
                :type="input.type"
                :multiple="input.multiple"
                :options="input.options"
                :buttons="input.buttons"
                />
            </b-input-group>
        </b-form-group>
        <b-button v-if="config.submitText" type="submit" variant="primary">{{config.submitText}}</b-button>
        <b-button v-if="config.resetText" type="reset" variant="primary">{{config.resetText}}</b-button>
    </b-form>
</template>

<script>
/*TODO:
    revisar que los validadores funcionen correctamente en todos los tipos.
    implementar sobrecarga de validadores para tipos nuevos.
    temrinar de implementar file, checkbox, radio y textarea.
    implementar tablas con formularios (posiblemente otro componente)
        verificar como opera invalidFeedback con objetos embebidos
*/
import moment from "moment";
let components = {};
export default {
    name:"ITDBForm",
    props:["config","form","value","double_check"],
    beforeCreate(){
        console.log("ITDFORM before create");
        //this.$set(this,"double_check",{});
        console.log(this);
        Vue.component("Functions",()=>import('@/components/Functions'));
        /*
         *import('@/components/Functions').then(component=>{
         *    Vue.component("Functions",component);
         *    components.Functions = component;
         *    //components.Functions = this.$createElement(component);
         *    //components.Functions = Vue.component("Functions");
         *    console.log("COMPONENT");
         *    console.log(components);
         *});
         */

    },
    created(){
        console.log(" created");
    },
    beforeMount(){
        console.log("before mount");
    },
    mounted(){
        console.log("mounted");
        console.log(this.$listeners);
    },
    /*
     *computed:{
     *    ...components
     *},
     */
    data () {
        return {
            myValue:{...this.value},
            myDouble_check:{...this.double_check},
        }
    },
    methods: {
        inputType(input){
            let pre="b-form-";
            switch(input.type){
                case "select":
                case "checkbox":
                case "checkbox-group":
                case "radio-group":
                case "file":
                    return pre+input.type;
                    break;
                case "boolean":
                    return pre+"checkbox";
                    break;
                case "text":
                case "email":
                case "password":
                case "number":
                case "date":
                    return pre+"input";
                    break;
                default:
                    //en caso de usar un tipo custom
                    return input.type;
            }
        },
        validateForm(){
            let text = this.form.reduce((TXT,{error:txt})=>{
                TXT+=((TXT && txt)?", ":"")+(txt||"");
                return TXT;
            },"").trim();
            let result = {state:text.length===0,text};
            console.log("ValidateForm");
            console.log(result);
            return result;
        }
    },
    watch:{
        myValue:{
            handler(val){
                console.log("BFORM myValue ha cambiado");
                console.log(this.myValue);
                this.$emit("input",this.myValue);
            },
            deep:true
        },
        myDouble_check:{
            handler(val){
                console.log("BFORM double_check ha cambiado");
                console.log(this.myDouble_check);
                this.$emit("input",this.myValue);
            },
            deep:true
        },
    }
}
</script>



