'use strict';


export default {
    namespaced:true,
    state:{//acá van los options según su nombre de referencia
        connect:false,
        test:null,
    },
    mutations:{
        SOCKET_CONNECT: (state,  status ) => {
            console.log("SOCKET.IO");
            console.log(status);
            state.connect = true;
        },
        SOCKET_TEST:(state,msg)=>{
            console.log("SOCKET.IO mutation");
            console.log(msg);
            state.null = msg;
        }
    },
    actions:{
        socket_test(context,message){
            console.log("SOCKET.IO action");
            console.log(message);
            context.displatch("TEST",{text:"texto de vuelta"});
        }
    }
};



