<template>
<form @submit="onSubmit" @reset="onReset">
    <div v-for="(input,id) in validator.value">
        <mdl-textfield 
            :key="id"
            v-bind="input"
            :ref="input.name"
            :floating-label="input.label"
            :textarea="input.type==='textarea'"
            :error="input.error"
            v-model="input.value"
            v-validate.native="input"
    
        />
        <mdl-textfield 
            v-if="input.validate && input.validate.double_check"
            v-bind="input"
            :floating-label="input.label"
            :textarea="input.type==='textarea'"
            v-validate.native="input"
            :error="input.error"
            v-model="input.double_check"
    
        />
    </div>
    <mdl-button colored primary raised type="submit" >{{FormDefinition.submitText}}</mdl-button>
    <mdl-button colored type="reset" >{{FormDefinition.resetText||"Limpiar"}}</mdl-button>
</form>
</template>

<script>
/*TODO:
    revisar que los validadores funcionen correctamente en todos los tipos.
    implementar sobrecarga de validadores para tipos nuevos.
    temrinar de implementar file, checkbox, radio y textarea.
    implementar tablas con formularios (posiblemente otro componente)
        verificar como opera invalidFeedback con objetos embebidos
*/
import moment from "moment";
export default {
    props:["FormDefinition","value"],
    created(){
        this.$validator.config(this.value,this.double_check);
        console.log("REFS");
        console.log(this.$refs);
    },
    data () {
        return {
        }
    },
    methods: {
        inputType(type){
            switch(type){
                case "select":
                case "checkbox":
                case "checkbox-group":
                    return type;
                    break;
                case "text":
                case "email":
                default:
                    return "input";
            }
        },
        onSubmit (evt) {
            this.$emit("submit",evt);
        },
        onReset (evt) {
            console.log("reset");
            this.$emit("reset",evt);
        },
        validateForm(){
            return this.$validator.validateForm(this.value);
        }
    },
}
</script>



