<template>
    <modal v-show="loading">
        <ring-loader :color="color" :size="size"/>
    </modal>
</template>

<script>
import VueResource from "vue-resource";
import Modal from "@/components/Modal";
import RingLoader from 'vue-spinner/src/RingLoader.vue';
import { createNamespacedHelpers } from 'vuex'
const {mapState, mapActions, mapMutations} = createNamespacedHelpers("loading");

const store = {
    namespaced: true,
    state:{
        loading:false,
        loadCount:0,
    },
    mutations:{
        start(state){
            state.loading=true;
            state.loadCount++;
        },
        stop(state){
            state.loadCount--;
            if(state.loadCount===0){
                console.log("todo cargado");
                state.loading=false;
            } 
        }
    }
};

export  default {
    name:"Loading",
    components:{Modal,RingLoader},
    beforeCreate(){
        this.$store.registerModule("loading",store);
    },
    data(){
        return {
        };
    },
    computed:{
        ...mapState(["loading","loadCount"]),
        color(){
            return this.$attrs.color||"#FF0000";
        },
        size(){
            return this.$attrs.color||"200px";
        }
    },
    methods:{
        ...mapMutations(["startLoading","stopLoading"]),
    },
}
</script>



