<template>
    <api-request :resource="getReady" v-model="ready">
        <b-navbar-nav v-if="ready">
              <b-nav-item-dropdown text="Menu" >
                  <!--b-dropdown-header>Dropdown header</b-dropdown-header-->
                  <b-dropdown-item 
                      v-for="{path,name,hidden,icon,description} in userData.functions"  
                      v-if="!hidden"
                      :key="path"
                      :to="path">
                      <i v-if="icon" :class="'fas fa-'+icon"></i> {{description||name}}
                </b-dropdown-item>
              </b-nav-item-dropdown>
              <b-nav-item v-for="{path,name,inNav,icon,description} in userData.functions" v-if="inNav" :to="path" :key="path+'Nav'">
                  <i v-if="icon" :class="'fas fa-'+icon"></i> {{name}}
              </b-nav-item>
        </b-navbar-nav>
    </api-request>
</template>

<script>
import { createNamespacedHelpers } from 'vuex'
const {mapState, mapActions} = createNamespacedHelpers("auth");
export default {
    name:"ITDMenu",
    data(){
        return {
            ready:false,
        }
    },
    computed:{
        ...mapState(["auth","userData"]),
    },
    methods:{
        getReady(){
            return new Promise(res=>{
                console.log("getReady");
                if(this.userData) res(true);
                else{
                    setTimeout(()=>res(this.getReady()),100);
                }
            });
        }
    },
    watch:{
        ready(val,old){
            console.log("READY");
            console.log(val,old);
        }
    }
}
</script>



