<template>
<div id="app">
    <auth option="_id" 
        model="User"
    />
    <!--auth/-->
    <loading :color="color" :size="size"/>
    <b-navbar 
        toggleable="sm" 
        type="dark" 
        variant="danger" 
        fill
        :sticky="sticky"
    >

    <b-navbar-toggle target="nav_collapse"></b-navbar-toggle>

        <b-navbar-brand to="/hello">
            <img src="/static/logo_web_DI.png" alt="BV">
        </b-navbar-brand>

        <b-collapse is-nav id="nav_collapse">
            <ITDMenu/>

            <!-- Right aligned nav items -->
            <b-navbar-nav class="ml-auto">

                <!--b-nav-form>
                    <b-form-input size="sm" class="mr-sm-2" type="text" placeholder="Search"/>
                    <b-button size="sm" class="my-2 my-sm-0" type="submit">Search</b-button>
                </b-nav-form-->
                <b-nav-item v-if="userData&& userData.kind">{{userData.kind||'Normal'}}</b-nav-item>

                <b-nav-item-dropdown text="Lang" right>
                    <b-dropdown-item href="#">EN</b-dropdown-item>
                    <b-dropdown-item href="#">ES</b-dropdown-item>
                    <b-dropdown-item href="#">RU</b-dropdown-item>
                    <b-dropdown-item href="#">FA</b-dropdown-item>
                </b-nav-item-dropdown>

                <b-nav-item-dropdown v-if="auth" right>
                    <!-- Using button-content slot -->
                    <template slot="button-content">
                        <em>{{(userData&&userData._id)||'Usuario'}}</em>
                    </template>
                    <b-dropdown-item href="#">Profile</b-dropdown-item>
                    <b-dropdown-item v-b-modal.authenticate>{{auth?"Signout":"Signin"}}</b-dropdown-item>
                </b-nav-item-dropdown>
                <b-nav-item v-else v-b-modal.authenticate right>Ingresar</b-nav-item>
            </b-navbar-nav>

        </b-collapse>
    </b-navbar>
    <b-container>
        <router-view :key="$route.fullPath"/>
    </b-container>
</div>
</template>

<script>
import ITDMenu from "@/components/ITDMenu";
import { createNamespacedHelpers } from 'vuex';
const {mapState, mapActions} = createNamespacedHelpers("auth");
const {mapState:myMapState} = createNamespacedHelpers("app");
export default {
    name: 'app',
    components:{ITDMenu},
    mounted(){
        console.log("APP");
        console.log(this.$store);
    },
    data(){
        return {
            menu:{},
            color:"#FF0000",
            size:"200px",
            authenticate:false,
        }
    },
    computed:{
        ...mapState(["auth","userData"]),
        ...myMapState(["sticky"])
    }
}
</script>




