<template>
  <div class="hello">
      <b-modal ref="loading" id="loading" no-close-on-esc no-close-on-backdrop hide-header-close>Cargando...</b-modal>
          <div style="width:600px">
              <api-request :resource="fetchDataForm" v-model=form>
                  <ITDForm v-if="form" @submit="onSubmit" @error="onError" @reset="onReset" :form="form" :config="config" v-model="value"/>
              </api-request>
          </div>
          <!--t>antes<p>wena mono</p><span>wena 2</span>despues<span><span>segundo nivel</span></span></t-->
  </div>
</template>

<script>
import ITDBForm from "./ITDBForm";
import ITDMDLForm from "./ITDMDLForm";

export default {
    name: 'HelloWorld',
    //components:{NestedList,MyForm},
    data () {
        console.log("data");
        return {
            msg: 'Welcome to Your Vue.js App',
            showNested:false,
            showForm:false,
            config:{
                submitText:"Enviar...",
                resetText:"Limpiar",
                range:{
                    0:ITDMDLForm,
                    600:ITDBForm
                }
            },
            value:{},
            form:null
        }
    },
    methods:{
        fetchDataList(){
            console.log("fetch");
            this.$http.get("/static/data.json").then(({body})=>{
                console.log(body);
                this.list=body.list;
                this.showNested=body.list.length;
                this.showNested=false;
            });
        },
        fetchDataForm(){
            console.log("fetch form");
            return this.$http.get("/static/form.json").then(({body})=>body);
        },
        onSubmit(evt){
            evt.preventDefault();
            alert(JSON.stringify(this.value,null,2));
        },
        onReset(evt){
            evt.preventDefault();
            console.log("limpiando form");
            console.log(this.form);
            this.$validator.copy_originalAll(this.form);
            console.log(this.form);
        },
        onError(evt){
            console.log("ERROR");
            console.log(evt);
        }
    },
    watch:{
        value:{
            handler(val){
                console.log("helloworld form change");
            },
            deep:true
        }
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



