<template>
<div>
    <ul v-if="list.length">
        <template v-for="(item,id) in list" >
        <li v-if="item.msg">{{item.msg}}<input v-model="item.msg"></li>
        <nested-list v-if="item.list" :initialList="item.list"/>
        </template>
    </ul>
    <b-button @click="send">enviar</b-button>
</div>
</template>

<script>
export default{
    name:"nested-list",
    props:["initialList"],
    data(){
        return {list:this["initialList"]};
    },
    methods:{
        send(){
            console.log(JSON.stringify(this.list));
        }
    }
};
</script>



