'use strict';

export default {
    namespaced:true,
    state:{
        auth:false,
        userData:null
    },
    mutations:{
        auth(state,data){
            state.auth=data.auth||false;
            state.userData=data.userData;
        }
    },
    actions:{
        checkLogin({commit,state},data){
            //verificamos si auth es false, de ser asi, entonces consultamos los datos de estado
            //si los datos son false o null entonces, todo se mantiene, de lo contrario, auth pasa a true y data a los datos
            //de sesion.
            console.log("checkLogin");
            console.log(state);
            if(!state.auth){
                console.log("no esta autenticado... pidiendo datos");
                return this._vm.$http[data?"post":"get"]("/APP/AAA/checkLogin.json",data).then(res=>{
                    console.log("resultado de la consulta de autenticación");
                    console.log(res.body);
                    commit("auth",res.body||null);
                    console.log(state);
                    return;
                });
            }else return Promise.resolve();
        },
    }
};



