<template>
    <!--keep-alive-->
        <transition name="bounce">
            <component v-show="show" ref="form" :is="getForm" :form="form" v-model="myValue" v-validate :double_check="double_check" :config="config" @submit="onSubmit" @reset="onReset" v-bind="$attrs"/>
        </transition>
    <!--/keep-alive-->
</template>

<script>
/*TODO:
    revisar que los validadores funcionen correctamente en todos los tipos.
    implementar sobrecarga de validadores para tipos nuevos.
    temrinar de implementar file, checkbox, radio y textarea.
    implementar tablas con formularios (posiblemente otro componente)
    verificar como opera invalidFeedback con objetos embebidos
 */
/*
    FormDefinition:{
        submit:<href>,
        submitText:<texto del botón submit>,
        resetText:<texto del botón reset, si no existe no va el botón>
        range:{
            <width>:<Form, el componente, si es objeto se usa, si es string debe ser el path desde donde importarlo>
            ...
        }
    }
*/
import moment from "moment";
import ITDBForm from "./ITDBForm";
import ITDMDLForm from "./ITDMDLForm";
import { createNamespacedHelpers } from 'vuex'
const {mapState, mapActions} = createNamespacedHelpers("form");
//TODO, PASAR TODOS LOS VUEX a store!!!
export default {
    name:"ITDForm",
    props:{
        config:{
            type:Object,
            default(){
                return {
                    submitText:"Enviar",
                    resetText:"Limpiar",
                };
            },
            validator(value){
                //TODO por ahora retorna true, debe analizar la estructura del Objeto.
                return true;
            }
        },
        range:{
            type:Object,
            default(){
                return {
                    0:ITDMDLForm,
                    600:ITDBForm
                };
            }
        },
        form:{
            required:true,
            type:Array,
            validator(value){
                //TODO por ahora retorna true, debe analizar la estructura del Objeto.
                return true;
            }
        },
        strict:{
            type:Boolean,
            default:true
        },
        value:{
            type:Object,
            required:true
        }
    },
    data () {
        console.log("ITDFORM");
        console.log(this.$attrs);
        console.log(this.value);
        console.log(this.form);
        return {
            myValue:this.form.reduce((obj,input)=>Object.assign(obj,{[input.name]:this.value[input.name]||input.value||(input.multiple?[]:"")}),{}),
            double_check:this.form.filter(el=>el.validate&&el.validate.double_check).reduce((obj,input)=>Object.assign(obj,{[input.name]:this.value[input.name]||input.value||(input.multiple?[]:"")}),{}),
            show:true
        };
    },
    computed:{
        ...mapState(["option"]),
        getForm(){
            let size = this.$mq.resize;
            //Tomamos el primero de los segmentos mayor o igual.
            let components = this.range;
            let res =  size && components[Object.keys(components).map(el=>el*1).sort().reverse().find(el=>this.$mq.above(el))];
            console.log("GETFORM",res);
            return res;
        },
    },
    methods: {
        ...mapActions(["getOption"]),
        onReset (evt) {
            console.log("form reset");
            this.$emit("reset",evt);
        },
        onSubmit (evt) {
            console.log("onsubmit");
            let {state,text} = this.$refs.form.validateForm();
            if(state) this.$emit("submit",evt);
            else{
                evt.preventDefault();
                this.show=false;
                this.$nextTick().then(()=>this.show=true);
                this.$emit("error",text);
            }
        },
        validateForm(){
            let {state,text} = this.$refs.form.validateForm();
            if(state) return Promise.resolve(this.myValue);
            this.show=false;
            this.$nextTick().then(()=>this.show=true);
            return Promise.reject(text);
        },
        input(evt){
            console.log("INPUT!!!");
            console.log(this.myValue);
            console.log(this.double_check);
        }
    },
    watch:{
        myValue:{
            handler(val){
                console.log("myValue ha cambiado");
                console.log(val);
                this.$emit("input",Object.assign(this.value,this.myValue));
            },
            deep:true,
            immediate:true
        },
        double_check:{
            handler(val){
                console.log("double_check ha cambiado");
                console.log(val);
                this.$emit("input",Object.assign(this.value,this.myValue));
            },
            deep:true
        }
    }
}
</script>y



