// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
const host="http://2ble_impacto.itdmovil.cl:8081";
import Vue from 'vue';
window.Vue = Vue;
import App from './App';
import router from './router';


import VueResource from "vue-resource";
Vue.use(VueResource);

import vMediaQuery from "v-media-query";
Vue.use(vMediaQuery);

import VueMDL from "vue-mdl";
Vue.use(VueMDL);

import BootstrapVue from "bootstrap-vue";
import 'bootstrap/dist/css/bootstrap.css';
import 'bootstrap-vue/dist/bootstrap-vue.css';
import 'vue2-animate/dist/vue2-animate.min.css';
Vue.use(BootstrapVue);

import VueApiRequest from 'vue-api-request';
Vue.use(VueApiRequest);


import store from '@/store';

import VueSocketio from 'vue-socket.io';
Vue.use(VueSocketio, host, store);


import NestedList from '@/components/NestedList';
import Translate from '@/components/Translate';
import Auth from '@/components/Auth';
import Validator from "@/plugins/validator";
import Loading from "@/components/Loading";
Vue.use(Validator,{isolated:false});
Vue.component('NestedList',NestedList);
Vue.component('t',Translate);
Vue.component('auth',Auth);
Vue.component("Loading",Loading);


Vue.config.productionTip = false;
//TWEEK para controlar las llamadas al FW en modo dev
const base = process.env.NODE_ENV!=="production"?host:"";
Vue.http.interceptor.before = (request,next)=>{
    request.url=base+request.url;
    next();
};
//TWEEK interceptor para llamadas http 
Vue.http.interceptors.push(function(request,next){
    store.commit("loading/start");
    next(response=>{
        store.commit("loading/stop");
        return response;
    });
});
if(process.env.NODE_ENV!=="production"){
    Vue.http.interceptors.push(function(request,next){
        next(response=>{
            if(response.status!==200){
                console.log("HTTP ERROR!!!");
                console.log("HTTP REQUEST...");
                console.log(request);
                console.log("response...");
                console.log(response);
                return response;
            }
        });
    });
}
/* eslint-disable no-new */
new Vue({
    el: '#app',
    router,
    store,
    template: '<App/>',
    components: { App }
});



