'use strict';


export default {
    namespaced:true,
    state:{//acá van los options según su nombre de referencia
        option:{},
        filter:{},
        create:{},
        fields:{}
    },
    mutations:{
        set(state,{model,type,data}){
            if(data) state[type] = {...state[type],[model]:data};
        }
    },
    actions:{
        getOption({commit,state},option){
            //veriticamos que exista la opción.
            console.log("getOption");
            console.log(option);
            if(state.option[option]){
                console.log("ya tengo valor, retorno directo");
                return Promise.resolve(state.option[option]);
            }
            //no existe la opción así que la pedimos.
            return this._vm.$http.post("/APP/AAA/getRef.json",{option}).then(({body})=>{
                //nos llegó la info
                console.log("resuldato del getRef para,",option);
                console.log(body);
                commit("set",{type:"option",model:option,data:body});
                console.log(state.option[option]);
                //TODO validar si es necesario agregar un timeout para eliminar el getRef y así tener un refresco.
                return state.option[option];
            })
        },
        getOptions({dispatch},form){
            console.log("getOptions");
            return Promise.all(form.filter(input=>input.ref).map(input=>{
                console.log("REF FOUND");
                console.log(input);
                return dispatch("getOption",input.ref).then(options=>input.options=options);
            })).then(()=>{
                console.log(form);
                return Promise.all(form.filter(el=>el.inputs).map(input=>{
                    return dispatch("getOptions",input.inputs);
                }));
            }).then(()=>form);
        },
        getJSON({commit,state},{model,type}){
            console.log("getJSON");
            if(state[type][model]){
                console.log("ya tengo",type,model);
                return Promise.resolve(state[type][model]);
            }
            return this._vm.$http.get(`/APP/AAA/${model}.json?type=${type}`).then(({body:data})=>{
                console.log("resultado de",type,model);
                console.log(data);
                commit("set",{type,model,data});
                console.log(state[type][model]);
                return state[type][model];
            });
        },
        getForm({dispatch},{model,type}){
            console.log("getForm");
            return dispatch("getJSON",{model,type}).then(form=>{
                return dispatch("getOptions",form).then(()=>form);
            });
        },
        getFilter({dispatch},model){
            return dispatch("getForm",{model,type:"filter"});
        },
        getCreate({dispatch},model){
            return dispatch("getForm",{model,type:"create"});
        },
        getFields({dispatch},model){
            return dispatch("getJSON",{model,type:"fields"});
        }
    }
};



